<template>
    <yd-layout :class="'unionCot'">
        <yd-navbar title="中国银联签报" bgcolor="#70b6ef" color="#fff" fontsize="0.36rem">
            <router-link to="/unionPayLabel/" slot="left">
                <yd-navbar-back-icon color="#fff">
                    <slot color="#fff">返回</slot>
                </yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
        <div class="mainContentFluid bor5">
            <div class="fluidWidth">
                <div class="advice">
                    <div class="commonAdvice">
                        常用意见
                    </div>
                    <div class="selectAdvice" v-on:click="statusPopup = true;btnStatusTxt='关闭状态'">
                        <yd-navbar-next-icon :class="'arrow'">请选择</yd-navbar-next-icon>
                    </div>
                </div>
                <div class="adviceTxt">
                    <yd-cell-group>
                        <yd-cell-item>
                            <span class="commonAdvice" slot="left">意见内容</span>
                            <yd-textarea slot="right" v-model="opinion" placeholder="请输入您的意见" :value="isAgree"></yd-textarea>
                        </yd-cell-item>
                    </yd-cell-group>
                </div>
                <div class="compl_back">
                    <div class="completeBtn" @click="completeFun">
                        完成审批
                    </div>
                    <div class="backBtn" @click="$router.go(-1)">
                        返回
                    </div>
                </div>
            </div>
        </div>
        <div class="mainContentFluid">
            <div class="fluidWidth leaderAdvice">
                <yd-flexbox direction="vertical">
                    <yd-flexbox-item v-if="liuyanFlag" :class="'fuyan'">
                        <yd-flexbox>
                            <div class="postscript">
                                上一步附言：
                            </div>
                            <div class="postscript_txt" v-text='liuyan'>
                            </div>
                        </yd-flexbox>
                    </yd-flexbox-item>
                    <yd-flexbox-item :class="'yijian'">
                        <yd-flexbox>
                            <div v-if="isLeader == 2" class="postscript otherLeader">
                                其他部门领导意见：
                            </div>
                            <div v-else class="postscript otherLeader">
                                本部门内意见：
                            </div>
                        </yd-flexbox>
                    </yd-flexbox-item>
                    <yd-flexbox-item>
                        <yd-flexbox>
                            <div style="width: 100%;">
                                <div class="label_box" v-for="item in opinio">
                                    <div class="range_txt attachment" v-text='item.neirong'></div>
                                    <div class="range_txt issueName">
                                        <div class="fl" v-text="item.partiname"></div>
                                        <div class="range issueTime" v-text='getDay(item.submitDate)'></div>
                                    </div>
                                </div>
                            </div>
                        </yd-flexbox>
                    </yd-flexbox-item>
                    <yd-flexbox-item :class="'fuyan'" v-if="deptOpinion !='' && deptOpinion != undefined">
                        <yd-flexbox>
                            <div class="postscript otherLeader">
                                部内意见：
                            </div>
                        </yd-flexbox>
                    </yd-flexbox-item>
                    <yd-flexbox-item v-if="deptOpinion !='' && deptOpinion != undefined">
                        <yd-flexbox>
                            <div style="width: 100%;">
                                <div class="label_box" v-for="item in deptOpinion">
                                    <div class="range_txt attachment" v-text='item.neirong'></div>
                                    <div class="range_txt issueName">
                                        <div class="fl" v-text="item.partiname"></div>
                                        <div class="range issueTime" v-text='getDay(item.submitDate)'></div>
                                    </div>
                                </div>
                            </div>
                        </yd-flexbox>
                    </yd-flexbox-item>
                </yd-flexbox>
            </div>
        </div>
        <yd-popup v-model="statusPopup" position="bottom" height="4rem">
            <div class="confirmBtn" slot="top">
                <div class="leftBtn" v-on:click="statusPopup = false">取消</div>
                <div class="rightBtn" v-on:click="statusPopup = false">确定</div>
            </div>
            <div class="selectStatus">
                <ul class="ul">
                    <li class="li" v-bind:class="{active:statusRadio==index}" v-for="(status,index) in agreeArr" v-on:click="isAgree=status;opinion=status;statusRadio=index;" v-text="status"></li>
                </ul>
            </div>
        </yd-popup>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'assistApprove',
    data() {
        return {
            statusRadio: 999,
            statusPopup: false,
            isAgree: '',
            liuyan: '',
            opinio: [],
            opinion: '',
            isLeader: '',
            agreeArr: [],
            liuyanFlag: true,
            leaderTypeArr: [],
            leaderNameArr: [],
            nextId: '',
            leadersId: '',
            approveNum: '',
            isApprove: false,
            isFinish: false,
            deparment: '',
            deparmentId: '',
            deptOpinion: [],
            deptname: '',
            cosnextId: '',
            common: ''
        }
    },
    mounted() {
        this.pageRender();
        document.setTitle(this.titleName);
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo',
            handleInfo: 'getHandleInfo',
            titleName: 'getTitleName'
        })
    },
    methods: {
        pageRender() {
            var vm = this;
            vm.isLeader = Number(vm.userInfo.isLeader);
            vm.deptname = vm.userInfo.deptname;
            // 上一步留言
            vm.getH(vm.conf.baseURL + '/oawf/httptrs/basehttp!queryOAPreviousFuyan.action', {
                'type': vm.userInfo.type,
                'workItemID': vm.userInfo.workItemID,
                'personID': vm.userInfo.userid
            }, function(res) {
                var data = res;
                if (!data.jsonData) {
                    vm.liuyan = '';
                    vm.liuyanFlag = false;
                } else {
                    vm.liuyan = data.jsonData[0].neirong;
                }
            }, function(res) {});
            // 领导意见
            vm.getH(vm.conf.baseURL + '/oawf/httptrs/basehttp!queryApproveByWorkItemID.action', {
                'type': vm.userInfo.type,
                'workItemID': vm.userInfo.workItemID
            }, function(res) {
                var data = res.jsonData;
                for (let i = 0; i < data.length; i++) {
                    if (vm.isLeader == 2) {
                        if (data[i].type1 == 2) {
                            vm.opinio.push(data[i]);
                        }
                    } else {
                        if (data[i].type1 != 2 && data[i].orgName == vm.deptname) {
                            vm.opinio.push(data[i]);
                        }
                    }
                }
            });
            // 部内意见
            vm.getH(vm.conf.baseURL + '/oawf/httptrs/basehttp!queryInnerInfo.action', {
                'type': vm.userInfo.type,
                'workItemID': vm.userInfo.workItemID
            }, function(res) {
                vm.deptOpinion = res.jsonData.opinions;
            });
            // 常用意见
            vm.getH(vm.conf.baseURL + '/oawf/httptrs/basehttp!queryPersonOpinion.action', {
                'type': vm.userInfo.type,
                'personID': vm.userInfo.userid
            }, function(res) {
                var data = res.jsonData;
                vm.agreeArr = data;
                if (vm.agreeArr.length < 0 || vm.agreeArr == '') {
                    vm.agreeArr = ['已阅', '同意', '同意发', '不同意', '退主办部门修改'];
                }
            });
            // 发送审批判断
            for (let key in vm.handleInfo.nextBtn) {
                if (key == 'doApprove') {
                    vm.isApprove = true;
                } else if (key == 'doFinishInner') {
                    vm.isFinish = true;
                }
            }
            if (vm.isApprove) {
                // 当有doApprove这个key的时候需要通过leaderTypeArr获取所有领导的名字
                for (let keyJ in vm.handleInfo.nextBtn) {
                    if (keyJ == 'doApprove') {
                        vm.approveNum = vm.handleInfo.nextBtn.doApprove.type;
                        if (vm.handleInfo.nextBtn.doApprove.queryHandlerPerson) {
                            vm.leaderTypeArr = vm.handleInfo.nextBtn.doApprove.queryHandlerPerson.split(','); // 获取领导接口的type
                        } else {
                            // 领导的nextbtn再坐判断 =={doApprove: {4094272: "廖雯", type: "2"}}
                            for (let keyId in vm.handleInfo.nextBtn.doApprove) {
                                if (keyId != 'type') {
                                    vm.leadersId = keyId;
                                }
                            }
                        }
                    } else {
                        vm.nextId = keyJ; // 下一步流程ID
                        vm.deparment = vm.handleInfo.nextBtn[keyJ][1];
                        vm.deparmentId = vm.handleInfo.nextBtn[keyJ][0]; // 部门ID
                    }
                }
                // 送领导审批 的领导名字
                var promise = new Promise(function(resolve, reject) {
                    for (let i = 0; i < vm.leaderTypeArr.length; i++) {
                        vm.getH(vm.conf.baseURL + '/oawf/httptrs/basehttp!queryHandlerPerson.action', {
                            'type': vm.userInfo.type,
                            'personID': vm.userInfo.userid,
                            'queryType': vm.leaderTypeArr[i]
                        }, function(res) {
                            var data = res.jsonData;
                            if (data !== null || data !== undefined) {
                                vm.leaderNameArr = vm.leaderNameArr.concat(data);
                            }
                            if (i == vm.leaderTypeArr.length - 1) {
                                resolve()
                            }
                        });
                    }
                });
                promise.then(function() {
                    vm.leaderNameArr = vm.leaderNameArr;
                });
            } else {
                // 没有doapprove的时候循环key 把key放到一个数组里面然后取第一个为部门，第二个为领导
                let keyArr = [];
                for (let keyK in vm.handleInfo.nextBtn) {
                    keyArr.push(keyK);
                }
                if (keyArr.length > 1) {
                    vm.nextId = keyArr[0]; // 部门下一步id
                    vm.deparment = vm.handleInfo.nextBtn[keyArr[0]][1]; // 部门或者人物名称
                    vm.deparmentId = vm.handleInfo.nextBtn[keyArr[0]][0]; // 部门或者人物Id
                    vm.cosnextId = keyArr[1]; // 领导下一步id
                    vm.leaderName3 = vm.handleInfo.nextBtn[keyArr[1]][1]; // 领导名字
                    vm.leadersId = vm.handleInfo.nextBtn[keyArr[1]][0]; // 领导id
                    vm.leaderNameArr = [{ 'empname': vm.leaderName3, 'operatorid': vm.leadersId }];
                    vm.leaderName3 = '(' + vm.handleInfo.nextBtn[keyArr[1]][1] + ')'; // 领导名字
                } else {
                    vm.leadersId = vm.handleInfo.nextBtn[keyArr[0]][0];
                    vm.nextId = keyArr[0];
                }
            }
        },
        getDay(date) {
            date = date.replace(/-/g, '/');
            return (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '日';
        },
        completeFun() {
            // 完成审批
            var vm = this;
            if (vm.opinion.replace(' ', '') != '') {
                if (vm.isApprove) {
                    vm.getH(vm.conf.baseURL + '/oawf/httptrs/basehttp!doApprove.action', {
                        'type': vm.userInfo.type,
                        'approveType': vm.approveNum,
                        'workItemID': vm.userInfo.workItemID,
                        'personID': vm.userInfo.userid,
                        'leaderIDs': vm.leadersId,
                        'opinion': vm.opinion
                    }, function(res) {
                        vm.$dialog.toast({
                            mes: '完成审批',
                            timeout: 2000,
                            callback: () => {
                                setTimeout(function () {
                                    vm.$router.push('/handle')
                                }, 0)
                            }
                        });
                    });
                } else if (vm.isFinish) {
                    vm.getH(vm.conf.baseURL + '/oawf/httptrs/basehttp!doFinishInner.action', {
                        'type': vm.userInfo.type,
                        'workItemID': vm.userInfo.workItemID,
                        'personID': vm.userInfo.userid,
                        'opinion': vm.opinion
                    }, function(res) {
                        vm.$dialog.toast({
                            mes: '完成审批',
                            timeout: 2000,
                            callback: () => {
                                setTimeout(function () {
                                    vm.$router.push('/handle')
                                }, 0)
                            }
                        });
                    });
                } else {
                    vm.getH(vm.conf.baseURL + '/oawf/httptrs/basehttp!doOANext.action', {
                        'type': vm.userInfo.type,
                        'workItemID': vm.userInfo.workItemID,
                        'leaderIDs': vm.leadersId,
                        'activityID': vm.nextId,
                        'opinion': vm.opinion,
                        'personID': vm.userInfo.userid
                    }, function(res) {
                        vm.$dialog.toast({
                            mes: '完成审批',
                            timeout: 2000,
                            callback: () => {
                                setTimeout(function () {
                                    vm.$router.push('/handle')
                                }, 0)
                            }
                        });
                    });
                }
            } else {
                vm.$dialog.toast({
                    mes: '请输入意见内容',
                    timeout: 2000
                });
            }
        }
    }
}
</script>
<style scoped>
.advice, .adviceTxt, .compl_back, .yijian, .fuyan{
    padding: 0 .33rem;
}
.fuyan{
    padding-bottom: .3rem;
}
.fuyan .postscript{
    padding-top: .3rem;
}
.adviceTxt{
    padding-top: .15rem;
}
.selectAdvice{
    color: #999;
    font-size: .32rem!important;
}

.adviceTxt .yd-cell-item{
    padding-left: 0!important;
}
.arrow i{
    color: #999;
}
.yijian{
    border-bottom: 0;
}
.li_first {
    padding-right: 0.57rem;
}
.bor5{
    border-bottom: .15rem solid #f1f1f1;
}
.yd-flexbox-item{
    
}
.mainContentFluid{
    margin-top: 0;
}
.li_second {
    padding-right: 0.3rem;
}

.compl_back {
    height: 0.9rem;
    line-height: 1.28rem;
    padding-top: .15rem;
}

.commonAdvice {
    max-width: 4.6rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: .32rem!important;
    color: #333!important;
    padding-left: 0!important;
}
.fluidWidth{
    padding-left: 0;
    padding-right: 0;
}

/* .selectStatus li:nth-child(1) {
    padding-right: 0.57rem;
}

.selectStatus li:nth-child(2) {
    padding-right: 0.3rem;
} */

.completeBtn {
    width: 2.36rem;
    height: 0.64rem;
    background-color: #61b3f6;
    line-height: 0.64rem;
    text-align: center;
    border-radius: 0.05rem;
    font-size: 0.3rem;
    color: #fff;
    letter-spacing: 0.04rem;
    margin-left: .65rem;
    float: left;
    cursor: pointer;
}

.backBtn {
    float: right;
    margin-right: 0.65rem;
    font-size: .3rem;
    letter-spacing: 0.1rem;
    cursor: pointer;
}

.postscript {
    font-size: 0.32rem;
    color: #999999;
    padding-top: .3rem;
}

.postscript_txt {
    font-size: 0.32rem;
    color: #333;
    padding-left: 0.35rem;
    padding-top: .3rem;
}

.otherLeader_txt {
    color: #1381dd;
    font-weight: bold;
    font-size: 0.28rem;
}

.leaderName {
    color: #333;
    font-size: 0.28rem;
    font-weight: bold;
}

.time {
    font-size: 0.28rem;
    color: #999999;
    margin-top: 0.4rem;
    float: right;
    padding-right: 0.2rem;
}

.yd-flexbox-item:nth-child(2) {
    border: none;
    padding-bottom: 0;
}

.comein{
    position: fixed;
    top:0;
    left:0;
    z-index: 9;
}

.leader .issueName {
    text-align: right;
    padding-right: 3rem;
}
.label_box{
    overflow: hidden;
    font-size: .32rem;
    position: relative;
    margin-top: .3rem;
    padding-bottom: .2rem;
    border-bottom: 1px dashed #eaeaea;
}
.attachment {
    color: #1381dd;
    max-width: 7.5rem;
    float: left;
    margin: 0;
    width: auto;
    margin-bottom: .1rem;
    font-size: .32rem;
    padding-left: 0;
}

.technique {
    color: #1381dd;
    font-weight: bold;
    max-width: 7.5rem;
}

.leaderAdvice .yd-flexbox {
    position: relative;
}
.leaderAdvice{
    padding: 0;
}

.issueTime {
    float: right;
    margin-top: 0;
    font-size: .32rem;
}
.fl{
    float: left;
    font-size: .32rem;
}
.issueName {
    margin-left: 4rem;
    margin-top: 0;
    color: #0a0a0a;
    margin-bottom: .1rem;
    overflow: hidden;
    height: auto;
}

</style>


